.form {
    flex-direction: column;
    display: flex;
    width: 20%;
    gap: 10px;
    margin-top: -35px;
    max-height: 400px;
    
    
    
    
}



@import url(https://fonts.googleapis.com/css?family=Merriweather);

html,
body {
    
    font-family: 'Merriweather', sans-serif;
    
}

h1 {
    text-align: center;
    color: #a8a8a8;
}

form {
    
  
    margin: 20px auto;
     
}

input,
textarea {
    border: 0;
    outline: 0;
    padding: 1em;
    border-radius: 8px;
    display: block;
    width: 100%;
    margin-top: 1em;
    font-family: 'Merriweather', sans-serif;
    resize: none;
}

#input-submit {
    color: white;
    background: #00CFBE;
    cursor: pointer;
}

#input-submit:hover {
    box-shadow: 0 1px 1px 1px rgba(0, 0, 0, 0.2);
}

textarea {
    height: 126px;
}

.half {
    float: left;
    width: 48%;
    margin-bottom: 1em;
}

.right {
    width: 50%;
}



@media (max-width: 480px) {
    .half {
        width: 100%;
        float: none;
        margin-bottom: 0;
    }
}

.cf:before,
.cf:after {
    content: ' ';
    display: table;
}

.cf:after {
    clear: both;
}